class AppRouter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      menu: false
    };
    this.toggleMenu = this.toggleMenu.bind(this);
  }

  toggleMenu() {
    this.setState({ menu: !this.state.menu });
  }
  render() {
    const show = this.state.menu ? "show" : "";
    return (
      <Router>
        <div>
          <nav className="navbar navbar-expand-md  navbar-fixed-top top-bar"  >
            <button class="navbar-toggler ml-auto custom-toggler" type="button" data-toggle="collapse" onClick={this.toggleMenu} data-target="#collapsingNavbar4">
        <span class="navbar-toggler-icon"></span>
    </button>
            <div class={"collapse navbar-collapse " + show}>
              <ul class="navbar-nav">
                <li class="nav-item active" onClick={this.toggleMenu} >
                  <Link to="/" className="nav-link"><b>
                    Home </b>
                  </Link>
                </li>
                <li class="nav-item" onClick={this.toggleMenu}>
                  <Link to="/Education" className="nav-link">
                    <b> Education</b>
                   
                  </Link>
                </li>
                <li class="nav-item" onClick={this.toggleMenu}>
                  <Link to="/Skills" className="nav-link">
                   <b> Skills </b>
                  </Link>
                </li>
                <li class="nav-item" onClick={this.toggleMenu}>
                  <Link to="/Project" className="nav-link">
                   <b> Project</b>
                  </Link>
                </li>
                <li class="nav-item" onClick={this.toggleMenu}>
                  <Link to="/Contact" className="nav-link">
                   <b> Blogs</b>
                  </Link>
                </li>
              </ul>
            </div>
          </nav>

          <Route path="/" exact component={Main} />
          <Route path="/Education/" component={Educations} />
          <Route path="/Skills/" component={Skill} />
          <Route path="/Project/" component={Projects} />
          <Route path="/Contact/" component={contacts} />
        </div>
      </Router>
    );
  }
}
export default AppRouter;
