import React from "react";
import "./Project.css";
import "../node_modules/@fortawesome/fontawesome-free/css/all.min.css";
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import img1 from "./img/trip.png";
import img2 from "./img/railway.jpg";
import img3 from "./img/bomberman.png";
import img4 from "./img/bricks.jpg";
import img5 from "./img/search.jpg";
import img6 from "./img/man.png";
import img7 from "./img/tic.gif";
import img8 from "./img/crud.jpeg";
const useStyles = makeStyles({
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});
export const Project = () => {
  const classes = useStyles();
  return (
    <div className="container">
    <div className="row">
      <div className="col-md-4">
    <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img1}
            title="Modern Ticketing"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Trip Analysis Using Modern Ticketing
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
             The digital solution for monitoring and analysing crowd of passengers in each route and provides a comfortable
             travel experience by allocating more bus in the crowded area and reducing the running of empty bus without passengers
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/TRIP-ANALYSIS-USING-MODERN-TICKETING"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
      <div className="col-md-4">
      <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img2}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Railway Reservation System
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
             The efficient way of booking the tickets using oops concept.The tickets are allocated based on priority,availability,re-allocation of seats once the
             seat is empty after certain stations.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{marginTop:"3rem"}}>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/Railway-Reservation-System"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
      <div className="col-md-4">
      <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img3}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
             Bomberman 
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              It is multi-player game which contains the path from start to end with some obstacles and moving ghosts.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{marginTop:"4rem"}}>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/Bomber-Man-Game"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
    </div>
    {/* <===================second===========> */}
    <div className="row">
      <div className="col-md-4">
    <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img4}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Civil Utility
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
             This app helps to calculate the number of bricks needed to build a wall or a building in a efficient way by getting
             the size of it as the input from user.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{marginTop:"3rem"}}>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/Bomber-Man-Game"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
      <div className="col-md-4">
      <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img5}
            title="Contemplative Reptile"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Theydal
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              The web app which shows the shop available nearby and current stock of the product.so that the user can choose which shop has the product based on his requirement.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{marginTop:"3rem"}}>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/Bomber-Man-Game"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
      <div className="col-md-4">
      <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img6}
            title="Contemplative Reptile"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Man
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              It's an online learning platform where the user can take the courses of their own and explore based on subscription.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{marginTop:"4rem"}}>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/MAN-E-Learning"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
    </div>
    {/* <==============third=======================> */}
    <div className="row">
      <div className="col-md-4">
    <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img7}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Tic-Tac-Toe
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
             It is a two player game where the player need to place X and O consequently until it is placed continuously
             in a row,column or diagonally.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{marginTop:"4rem"}}>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/Tic-Tac-Toe"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
      <div className="col-md-4">
      <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img8}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Library Management System
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              The CRUD operation is done in Library Management System where the books are added,updated,deleted and retrived based on needs.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{marginTop:"3rem"}}>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/Library-mangement-React"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
      <div className="col-md-4">
     
      </div>
    </div>
    </div>
  );
};
