import React from "react";
import '../node_modules/@fortawesome/fontawesome-free/css/all.min.css'; 
import './Contact.css';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import img3 from "./img/this.jpg";
const useStyles = makeStyles({
    card: {
      maxWidth: 345,
    },
    media: {
      height: 140,
    },
  });
  
  export  function MediaCard() {
    const classes = useStyles();
  
    return (
      <Card className={classes.card} style={{marginLeft:"1rem",marginTop:"3rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img3}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
            Do you know how interesting ‘this’-Keyword in JavaScript?
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
          <div className="social-icons" >
                    <a
                      href="https://medium.com/@aravinddhiva/do-you-know-how-interesting-this-keyword-in-javascript-a404697a6814"
                      target="_blank" 
                    >
                      <i className="fab fa-medium"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
    );
  }